@import "https://fonts.googleapis.com/css?family=Roboto:500";
body {
  margin: 0%;
}

.container {
  color: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 0% 20px 20px;
  font-family: Arial, sans-serif;
  display: flex;
}

.title {
  text-align: center;
  margin-bottom: 20px;
}

.pagination {
  justify-content: center;
  margin-top: 5px;
  display: flex;
}

.pagination-list {
  margin-top: 5px;
  margin-bottom: 5px;
  padding: 1px;
  list-style: none;
  display: flex;
}

.pagination-item.down-arrow {
  padding-top: 11px;
}

.pagination-item {
  color: #fff;
  cursor: pointer;
  background-color: #4b4b4b;
  border: 2px solid #fff;
  border-radius: 6px;
  align-items: center;
  margin: 3.5px;
  padding: 10px;
  transition: background-color .3s;
  display: flex;
}

.pagination-item:hover {
  background-color: #626262;
}

.pagination-item.active {
  color: #333;
