}

.pagination-item.disabled {
  opacity: 0;
  cursor: default;
}

.pagination-item:first-child {
  margin-left: 0;
}

.pagination-item:last-child {
  margin-right: 0;
}

.pagination-top {
  margin-inline-start: auto;
  background-color: #1d1e1f7b;
  border: 3px solid #000000a7;
  border-radius: 15px;
  margin-top: 0;
  padding: 0 8px;
}

.pagination.button-space {
  margin: 0;
  padding: 0;
}

.pagination-item.page-end {
  cursor: not-allowed;
  opacity: .25;
}

a {
